export default {
	data() {
		return {
			user_id: 0,
			img_object: [{}],
			/*img_object
			[{
				"id":
				"title":
				"img":
			},]
			*/
		}
	},

	onLoad: function(e) {
		
	},

	methods: {
		/*打开界面获取信息*/
		get_info(e) {
			uni.request({
				url: 'http://localhost/api/habit/present',
				method: 'GET',
				data:{
					uid:this.user_id
				},
				/*返回值*/
				success: (res) => {
					/*错误接收暂且不管*/
					// err = res.err,
					this.user_id = res.uid,
					this.img_object = res.data
				}
			})
		},

		/*点击修改*/
		click_img() {
			uni.navigateTo({
				url:'../../pages/index/edit_card?img_object'+img_object,
			})
		},
	}
}
